import PropTypes from "prop-types";
import { groupBy, maxBy, minBy, meanBy } from "lodash";

const ReportBody = txReport => {
  const groupedTx = groupBy(txReport, "name");

  const row = Object.keys(groupedTx).map((name, i) => (
    <tr key={i}>
      <th>{name}</th>
      <td>{groupedTx[name].length}</td>
      <td>{minBy(groupedTx[name], "duration").duration}</td>
      <td>{maxBy(groupedTx[name], "duration").duration}</td>
      <td>{meanBy(groupedTx[name], "duration").toFixed(2)}</td>
    </tr>
  ));

  return <tbody>{row}</tbody>;
};

const StatisticsBreakdown = ({ txReport }) => {
  return (
    <div className="row">
      <table className="table">
        <thead>
          <tr>
            <th scope="col">Transaction Type</th>
            <th scope="col">No. Execution</th>
            <th scope="col">Shortest Execution (ms)</th>
            <th scope="col">Longest Execution (ms)</th>
            <th scope="col">Average Execution (ms)</th>
          </tr>
        </thead>
        {ReportBody(txReport)}
      </table>
    </div>
  );
};

StatisticsBreakdown.propTypes = {
  txReport: PropTypes.array
};

export default StatisticsBreakdown;
